.header {
	background: url(../img/bg.jpg) no-repeat right top;
	height: 480px;
	padding-top: 15px;
	&__section {
		& h1 {
			font-size: 20px;
			font-weight: 800;
			color: #fff;
			text-align: center;
			margin-top: 60px;
			& span {
				color: #fe8057; } }
		& p {
			font-size: 12px;
			font-weight: 400;
			text-align: center;
			color: #f1f1f1;
			width: 304px;
			margin: 0 auto;
			margin-top: 35px;
			& img {
				display: none; } }
		& a {
			display: flex;
			justify-content: center;
			align-items: center;
			width: 300px;
			height: 40px;
			margin-top: 55px;
			background: #fe8057;
			border: 2px solid #fe8057;
			border-radius: 5px;
			font-size: 12px;
			font-weight: 400;
			color: #fff;
			text-transform: uppercase;
			cursor: pointer;
			transition: .2s;
			&:hover,
			&:focus {
				background: transparent;
				font-weight: 700; } }
		& > img {
			display: none; } }
	&__span {
		display: none; } }

@media (min-width: 1024px) {
	.header {
		background: url(../img/bg.jpg) no-repeat center top;
		background-size: cover;
		height: 778px;
		&__section {
			& h1 {
				font-size: 36px;
				text-align: left;
				margin-top: 70px;
				width: 500px;
				margin-left: auto; }
			& p {
				width: 440px;
				font-size: 14px;
				text-align: left;
				margin-right: 0; }
			& a {
				margin-left: auto;
				margin-right: 140px; }
			& > img {
				display: block;
				margin: auto;
				margin-top: 130px; } } } }

@media (min-width: 1440px) {
	.header {
		background: url(../img/bg.jpg) no-repeat center top;
		background-size: cover;
		height: 880px;
		&__section {
			& h1 {
				font-size: 42px;
				width: 620px;
				letter-spacing: 1px; }
			& p {
				width: 610px;
				font-size: 16px;
				display: flex;
				align-items: center;
				justify-content: center;
				& img {
					display: block;
					margin-top: -15px;
					margin-right: 20px; } }
			& a {
				margin-right: 110px; }
			& > img {
				margin-top: 200px; } }
		&__span {
			display: block;
			color: #fff;
			font-size: 12px;
			margin-top: 15px;
			text-align: center; } } }
